<template>
	<div class="address-city">
		<div class="container">
			<ButtonBack v-if="step === 'city'" class="button--arrow button--arrow-address">Назад</ButtonBack>
			<Button v-else-if="step === 'office'" :handler="back" class="button--arrow button--arrow-address">
				<svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"><mask id="mask0_126_4444" maskUnits="userSpaceOnUse" x="0" y="0" width="20" height="20" style="mask-type: alpha;"><rect x="20" y="20" width="20" height="20" transform="rotate(-180 20 20)" fill="url(#pattern0)"></rect></mask><g mask="url(#mask0_126_4444)"><rect x="20" y="21.25" width="20" height="21.25" transform="rotate(-180 20 21.25)" fill="#808080"></rect></g><defs><pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1"><use xlink:href="#image0_126_4444" transform="scale(0.00195312)"></use></pattern><image id="image0_126_4444" width="512" height="512" xlink:href="data:image/png;base64,"></image></defs></svg>
				Вернуться назад
			</Button>
		</div>
		<div class="address-city__container">
			<div class="address-city__actions" v-if="view !== 'address-slider'">
				<Button class="button--address map" :handler="() => view = 'map'">
					<img src="./map_tab.png" alt=""/>
					На карте
				</Button>
				<Button class="button--address office" :handler="() => view = 'office'">
					<img src="./office_list.png" alt=""/>
					Список офисов
				</Button>
			</div>

			<template v-if="view === 'map'">
				<div class="address-city__map">
						<yandexMap
							:coords="yaMapsData.coords"
							:settings="yaMapsData.settings"
							:zoom="yaMapsData.zoom"
							@map-was-initialized="setMapSettings"
							style="height: 100%"
							v-if="showMap"
						>
							<ymapMarker
								v-for="marker in yaMapsData.markers"
								:key="marker.id"
								:coords="marker.coords"
								:icon="yaMapsData.icon"
								:marker-id="marker.id"
								@click="() => setSingleMarker(marker.id)"
							>
							</ymapMarker>
						</yandexMap>
				</div>

				<div class="address-city__footer" :class="{'office': step === 'office'}">
					<div class="address-city__footer-wrap container" :class="{'office': step === 'office'}">
						<template v-if="step === 'city'">
							<Button class="button--address-city address-city__name" :handler="showComponentHandler">
								{{store.state.addresses.initialCity}}
								<img src="./arrow-down.png" alt="">
							</Button>
							<p class="address-city__office-choice">Выберите ближайший офис на карте</p>
							<Button class="button--address-city-payment address-city__payment" :handler="() => router.push({ name: 'payment' })">Вернуться к оплате</Button>
						</template>
						<template v-else-if="step === 'office'">
							<div @click="() => setSliderView(currentOfficeId)">
								<strong class="address-offices__current-office-street">{{formatAddress(currentOffice.address_data.data.serialize)}}</strong>
								<p class="address-offices__current-office-waypoint" v-if="currentOffice.waypoint">{{currentOffice.waypoint}}</p>
								<p class="address-offices__current-office-busstop" v-if="currentOffice.bus_stop">ост {{currentOffice.bus_stop}}</p>
								<div class="address-offices__work-schedule">
									<div v-for="(schedule, i) in currentOffice.schedule" :key="i">
										<span class="address-offices__day-label">{{ formatWeekday(schedule) }}:</span>
										<span class="address-offices__work-time">{{ formatWeekTime(schedule) }}</span>
									</div>
								</div>
								<div class="address-offices__current-office-holiday" v-if="!currentOffice.holiday_schedule.length > 0">
									График работы в праздничные дни
								</div>
								<Button :class="{'office': step === 'office'}" class="button--address-city-payment address-city__payment" :handler="() => router.push({ name: 'payment' })">Вернуться к оплате</Button>
							</div>
						</template>
					</div>
				</div>
			</template>
			<template v-else-if="view === 'address-slider'">
				<AddressCurrentWithSlider @setView="(arg) => view = arg" :currentOfficeId="currentOfficeId">
					<template v-slot:slider v-if="officeImages.length > 0">
						<AddressSlider :officeImages="officeImages"/>
					</template>
					<template v-slot:slider-no-image v-if="officeImages.length === 0">
						<div style="height: 148px; width: 100%; background: #D9D9D9; display: flex; justify-content: center; align-items: center">Нет Изображений</div>
					</template>

				</AddressCurrentWithSlider>
			</template>
			<template v-else-if="view === 'office'">
				<div class="address-offices">
					<div class="address-offices__header">
						<span class="address-offices__city-label">Ваш город:</span>
						<Button class="button--address-city button--address-office" :handler="showComponentHandler">
							{{store.state.addresses.initialCity}}
							<img src="./arrow-down.png" alt="">
						</Button>
						<p class="address-offices__departments">{{departmentsAmount}} {{declDepartments(departmentsAmount)}}</p>
					</div>
					<ul class="address-offices__list">
						<li
							class="address-offices__item"
							v-for="office in currentCityOffices"
							:key="office.id"
							@click="() => setSliderView(office.id)"
						>
							<strong class="address-offices__street">{{formatAddress(office.address_data.data.serialize)}}</strong>
							<div class="address-offices__work-schedule">
								<div v-for="(schedule, i) in office.schedule" :key="i">
									<span class="address-offices__day-label">{{ formatWeekday(schedule) }}:</span>
									<span class="address-offices__work-time">{{ formatWeekTime(schedule) }}</span>
								</div>
							</div>
							<p class="address-offices__details">оплата и выдача наличными</p>
						</li>
					</ul>
					<Button class="button--office-list-payment" :handler="() => router.push({ name: 'payment' })">Вернуться к оплате</Button>
				</div>
			</template>

			<SlideUpPopup
				v-if="store.state.addresses.showCitiesList"
				:hideComponentHandler="hideComponentHandler"
				class="slide-up-popup--address-city"
				:extraClass="'address-city'"
			>
				<Input type="text" v-model="city" :extraClass="'address-city'" placeholder="Введите город"/>
				<div class="address-city__empty" v-if="cities.length === 0">
					Ничего не найдено
				</div>
				<ul v-else class="address-city__list">
					<li v-for="city in cities" :key="city.city" class="address-city__list-item" @click="() => setCity(city.city)">
						<p class="address-city__popup-city">{{city.city}}</p>
						<p class="address-city__popup-region">{{(city.type).toLowerCase()}} {{city.name}}</p>
					</li>
				</ul>
			</SlideUpPopup>
		</div>
	</div>
</template>

<script setup>
import SlideUpPopup from '@/shared/components/SlideUpPopup';
import Input from '@/shared/components/Input/Input.vue';
import ButtonBack from '@/shared/components/ButtonBack';
import Button from '@/shared/components/Button';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
import {
	computed, onMounted, reactive, ref, watch,
} from 'vue';
import { declDepartments } from '@/shared/lib/helpers/declension';
import { yandexMap, ymapMarker } from 'vue-yandex-maps';
import AddressCurrentWithSlider	from '@/entities/components/AddressCurrentWithSlider';
import AddressSlider from '@/entities/components/AddressSlider';

const router = useRouter();
const store = useStore();
const city = ref('');
const view = ref('map');
const currentOfficeId = ref(1);
const step = ref('city');
const officeMarkers = ref([]);
const officeImages = ref([]);
const yaMapsData = reactive({
	addressCity: null,
	zoom: 1,
	icon: {
		layout: 'default#image',
		imageHref: 'https://my.incognitocr.ru/img/marker.png',
		imageSize: [30, 40],
	},
	markers: [],
	coords: [54.73404, 55.958008],
	settings: {
		apiKey: '963de105-2318-4436-a387-407418ec4f27',
		lang: 'ru_RU',
		coordorder: 'latlong',
		enterprise: false,
		version: '2.1',
	},
});
const showMap = ref(false);

const departmentsAmount = computed(() => (store.getters['addresses/getCurrentCityOffices'] ? store.getters['addresses/getCurrentCityOffices'].length : null));
const currentCityOffices = computed(() => store.getters['addresses/getCurrentCityOffices']);
const currentOffice = computed(() => currentCityOffices.value.find((office) => office.id === currentOfficeId.value));

const setMapSettings = () => {
	yaMapsData.markers = officeMarkers.value;
	yaMapsData.coords = officeMarkers.value[0].coords;
	showMap.value = true;

	if (officeMarkers.value.length > 1) {
		yaMapsData.zoom = 10;
	} else {
		yaMapsData.zoom = 18;
	}
};

const setAllMarkers = () => {
	officeMarkers.value = currentCityOffices.value.map((office) => ({
		id: office.id,
		coords: [office.latitude, office.longitude],
	}));
	// setMapSettings();
};

const setSingleMarker = (officeId) => {
	console.log('marker id: ', officeId);
	currentOfficeId.value = officeId;

	officeMarkers.value = [currentCityOffices.value.map((office) => ({
		id: office.id,
		coords: [office.latitude, office.longitude],
	})).find((marker) => marker.id === officeId)];

	setMapSettings();
	step.value = 'office';
};

const setSliderView = (officeId) => {
	view.value = 'address-slider';
	setSingleMarker(officeId);
};

onMounted(async () => {
	await store.dispatch('addresses/getAddresses');
	setAllMarkers();
	setMapSettings();
});

const hideComponentHandler = () => {
	setTimeout(() => {
		store.commit('addresses/SHOW_CITIES_LIST', false);
		city.value = '';
	}, 300);
};
const showComponentHandler = () => {
	store.commit('addresses/SHOW_CITIES_LIST', true);
};

const setCity = (cityName) => {
	store.commit('addresses/SET_CITY', cityName);
	hideComponentHandler();
	setAllMarkers();
	setMapSettings();
	step.value = 'city';
};
const back = () => {
	setCity(store.state.addresses.initialCity);
	officeImages.value = [];
	view.value = 'map';
};
watch(
	() => store.state.showCitiesList,
	(show) => {
		document.body.style.overflow = show ? 'hidden' : 'auto';
	},
);

const getOfficeImages = () => {
	const pics = currentOffice.value.pictures;

	if (pics && pics.length > 0) {
		pics.forEach(async (file) => {
			const response = await store.dispatch('addresses/getOfficeImages', { picture: file });
			officeImages.value.push({ image: response });
		});
	}
};

watch(
	view,
	(viewVal) => {
		if (viewVal === 'address-slider') {
			getOfficeImages();
		}
		document.body.style.overflow = viewVal === 'address-slider' ? 'hidden' : 'auto';
	},
);

const formatAddress = (data) => {
	let resultAddress = '';

	const cityValue = data.find(
		(item) => (item.name && item.level === 'City')
			|| (item.name && item.level === 'Place'),
	);
	const street = data.find((item) => item.level === 'Street' && item.name);
	const house = data.find((item) => item.level === 'House' && item.name);
	const building = data.find((item) => item.level === 'Building' && item.name);

	if (cityValue) {
		resultAddress += `${cityValue.type}. ${cityValue.name}, `;
	}
	if (house) {
		resultAddress += `${street.type} ${street.name}, `;
	}
	if (house) {
		resultAddress += `${house.name} `;
	}
	if (building) {
		resultAddress += `${building.type} ${building.name}`;
	}

	return resultAddress;
};

const formatWeekday = ({ weekday }) => {
	const days = ['пн', 'вт', 'ср', 'чт', 'пт', 'сб', 'вс'];

	const numToDays = weekday.map((item) => days[item - 1]);
	return `${numToDays[0]}-${numToDays[numToDays.length - 1]}`;
};

const formatWeekTime = ({ end, start }) => {
	if (end === '00:00' && start === '00:00') {
		return 'выходной';
	}
	return `c ${start} до ${end}`;
};

const cities = computed(() => {
	if (!city.value) {
		return store.getters['addresses/getOfficeCities'];
	}

	return store.getters['addresses/getOfficeCities'].filter((item) => item.city.toLowerCase().includes(city.value.toLowerCase()));
});

</script>

<style lang="scss" scoped>
@import "address-city";

.yandex-container {
	height: 400px;
}

.yandex-balloon {
	height: 200px;
	width: 200px;
}
</style>
