<template>
	<div class="address-current-with-slider">
		<slot name="slider"></slot>
		<slot name="slider-no-image"></slot>
		<div class="address-current-with-slider__wrap">
			<div v-for="n in 3" :key="n">
				<i style="display: none">{{n}}</i>
				<p class="address-current-with-slider__office-street">{{formatAddress(currentOffice.address_data.data.serialize)}}</p>
				<p class="address-current-with-slider__office-waypoint" v-if="currentOffice.waypoint">{{currentOffice.waypoint}}</p>
				<p class="address-current-with-slider__office-busstop" v-if="currentOffice.bus_stop">ост {{currentOffice.bus_stop}}</p>
				<div class="address-current-with-slider__office-actions">
					<Button class="button--address-current-with-slider" :handler="() => $emit('setView', 'map')">Показать на карте</Button>
					<Button class="button--address-current-with-slider" :handler="() => ({})">Маршрут</Button>
				</div>
				<div class="address-current-with-slider__work-schedule">
					<div v-for="(schedule, i) in currentOffice.schedule" :key="i">
						<span class="address-current-with-slider__day-label">{{ formatWeekday(schedule) }}: </span>
						<span class="address-current-with-slider__work-time">{{ formatWeekTime(schedule) }}</span>
					</div>
				</div>
				<div class="address-current-with-slider__current-office-holiday-headline" v-if="!currentOffice.holiday_schedule.length > 0">
					График работы в праздничные дни
				</div>
				<p class="address-current-with-slider__pay-cash">Оплата и выдача наличными</p>
			</div>
		</div>
		<Button class="button--office-list-payment" :handler="() => router.push({ name: 'payment' })">Вернуться к оплате</Button>
	</div>
</template>

<script setup>
import Button from '@/shared/components/Button';
import { useRouter } from 'vue-router';
import { computed, defineProps } from 'vue';
import { useStore } from 'vuex';

const router = useRouter();
const store = useStore();

const props = defineProps({
	currentOfficeId: {
		type: Number,
	},
});
const currentCityOffices = computed(() => store.getters['addresses/getCurrentCityOffices']);
const currentOffice = computed(() => currentCityOffices.value.find((office) => office.id === props.currentOfficeId));

const formatWeekday = ({ weekday }) => {
	const days = ['пн', 'вт', 'ср', 'чт', 'пт', 'сб', 'вс'];

	const numToDays = weekday.map((item) => days[item - 1]);
	return `${numToDays[0]}-${numToDays[numToDays.length - 1]}`;
};

const formatWeekTime = ({ end, start }) => {
	if (end === '00:00' && start === '00:00') {
		return 'выходной';
	}
	return `c ${start} до ${end}`;
};

const formatAddress = (data) => {
	let resultAddress = '';

	const cityValue = data.find(
		(item) => (item.name && item.level === 'City')
			|| (item.name && item.level === 'Place'),
	);
	const street = data.find((item) => item.level === 'Street' && item.name);
	const house = data.find((item) => item.level === 'House' && item.name);
	const building = data.find((item) => item.level === 'Building' && item.name);

	if (cityValue) {
		resultAddress += `${cityValue.type}. ${cityValue.name}, `;
	}
	if (house) {
		resultAddress += `${street.type} ${street.name}, `;
	}
	if (house) {
		resultAddress += `${house.name} `;
	}
	if (building) {
		resultAddress += `${building.type} ${building.name}`;
	}

	return resultAddress;
};
</script>

<style lang="scss" scoped>
@import "address-current-with-slider";
</style>
