<template>
	<div class="input-wrap" :class="extraClass">
		<input
			v-maska="setMask"
			:value="modelValue"
			@input="setValue"
			@focus="onFocus"
			@blur="onBlur"
			:disabled="disabled"
			:autocomplete="autocomplete"
			:type="type"
			class="input"
			:class="extraClass"
		/>
		<div v-if="placeholderVisible" class="input-placeholder" :class="extraClass">{{placeholder}}</div>
	</div>
</template>

<script setup>
import {
	defineProps, defineEmits, ref, computed, watchEffect,
} from 'vue';

const props = defineProps({
	placeholder: {
		type: String,
		default: 'Placeholder',
	},
	modelValue: {
		type: [String, Number],
		default: '',
	},
	disabled: {
		type: Boolean,
		default: false,
	},
	autocomplete: {
		type: String,
	},
	type: {
		type: String,
		default: 'text',
	},
	mask: {
		type: String,
		default: '',
	},
	maskTokens: {
		type: Object,
	},
	extraClass: {
		type: String,
		default: '',
	},
});

const emit = defineEmits(['update:modelValue']);

const placeholderVisible = ref(true);

const setMask = computed(() => {
	if (props.mask && props.maskTokens) {
		return {
			mask: props.mask,
			tokens: props.maskTokens,
		};
	}

	return null;
});

const setValue = (event) => {
	placeholderVisible.value = false;
	emit('update:modelValue', event.target.value);
};

const onFocus = () => {
	placeholderVisible.value = false;
};
const onBlur = () => {
	placeholderVisible.value = props.modelValue.length <= 0;
};

watchEffect(() => {
	if (props.modelValue.length > 0) {
		placeholderVisible.value = false;
	}
});
</script>

<style lang="scss" scoped>
@import "input";
</style>
